import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { InputGroupInline, InputType, REFERENCE, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { ANALITICO, SINTETICO_ANALITICO_VALUES } from '../../../../../models/enumerated/mcg/sinteticoAnaliticoTipoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, DemonstrativoCstIcmsIpiRV, REPORT_EVENT, REPORT_TITLE } from './demonstrativoCstIcmsIpiRV';

const DEFAULT_VALUES: DemonstrativoCstIcmsIpiRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  isIcms: true,
  isIpi: true,
  tipoRel: ANALITICO.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: true,
};

const DemonstrativoCstIcmsIpiReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<DemonstrativoCstIcmsIpiRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = (formData: DemonstrativoCstIcmsIpiRV): string[] => {
    const errors = [];
    if (!(formData.isIcms || formData.isIpi)) {
      errors.push('Produtos com IPI ou Produtos com ICMS - Pelo menos uma opção deve ser selecionada.');
    }
    return errors;
  };

  useEffect(
    () => {
      if (formProps.getValues().tipoRel === '2') {
        formProps.setValue('title', 'Demonstrativo CST ICMS/IPI Sintético');
      } else {
        formProps.setValue('title', 'Demonstrativo CST ICMS/IPI Analítico');
      }
      formProps.triggerValidation('title');
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().tipoRel]
  );

  return (
    <WindowComponent
      title="Demonstrativo CST ICMS/IPI"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkIsIcms" label="Produtos com ICMS" name="isIcms" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>

        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkIsIpi" label="Produtos com IPI" name="isIpi" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>

        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkIsBreakPage" label="Quebrar Página entre Impostos" name="isBreakPage" innerRef={formProps.register} type="switch" />
          </Col>
        </FormGroup>

        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="checkIsBreakPageEntradaSaida"
              label="Quebrar Página entre Entrada e Saída"
              name="isBreakPageEntradaSaida"
              innerRef={formProps.register}
              type="switch"
            />
          </Col>
        </FormGroup>

        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="checkIsExibirCstsIsentas"
              label="Exibir produtos com CST isenta"
              name="isExibirCstsIsentas"
              innerRef={formProps.register}
              type="switch"
            />
          </Col>
        </FormGroup>
        <InputGroupInline
          id="tipoRel"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Tipo de impressão"
          inputDropDownProps={{
            optionsProvider: SINTETICO_ANALITICO_VALUES,
            selectProps: { autoFocus: true, isClearable: false },
          }}
          isPrependLabel={true}
          placeholder="Informe o Tipo de impressão"
          type={InputType.DROP_DOWN}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default DemonstrativoCstIcmsIpiReportView;
